import React from 'react';
import {Loading, Ueditor} from '../../components';
import {Form, FormGroup, ControlLabel, Grid, Col, Button} from 'react-bootstrap';

const Post = React.createClass({
    getInitialState() {
        return {
            sort: ['', '', '']
        };
    },
    getDefaultProps(){
        return {
            loading: false,
            data: {
                title: '',
                tags: '',
                post: '',
                sort: '',
                description: ''
            }
        }
    },
    submitHandle(e){
        e.preventDefault();
        var contentTxt = this.refs.editor.state.editorDOM.getContentTxt();
        var description = contentTxt.length > 100 ? contentTxt.substring(0, 200) : contentTxt; //100
        if (!contentTxt) {
            //
            return;
        }
        let formParams = {
            title: this.refs.title.value,
            tags: this.refs.tags.value.split(','),
            post: contentTxt,
            sort: this.refs.sort.value,
            description: description
        }
        this.props.onSubmit(formParams);
    },
    render() {
        var that = this;
        if (this.props.loading) {
            return <Loading/>
        }
        return (
            <Grid>
                <Form horizontal onSubmit={this.submitHandle}>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={2}>
                            
                        </Col>
                        <Col sm={10}>
                            <input className="form-control" ref="title" defaultValue={this.props.data.title} placeholder=""/>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={2}>
                            
                        </Col>
                        <Col sm={10}>
                            <input className="form-control" ref="tags" defaultValue={this.props.data.tags} placeholder=""/>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={2}>
                            
                        </Col>
                        <Col sm={10}>
                            <select className="form-control" ref="sort" placeholder="" defaultValue={that.props.data.sort}>
                                {this.state.sort.map(function (item, index) {
                                    return (
                                        <option key={index} value={item}>{item}</option>
                                    )
                                })}
                            </select>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={2}>
                            
                        </Col>
                        <Col sm={10}>
                            <Ueditor ref="editor" content={this.props.data.post}/>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col smOffset={2} sm={10}>
                            <Button type="submit">
                                
                            </Button>
                        </Col>
                    </FormGroup>
                </Form>
            </Grid>
        )
    }
})
export default Post;


