import React from 'react';
import {Form, FormGroup, ControlLabel, Grid, Col, Button} from 'react-bootstrap';
const LoginForm = React.createClass({
    submitHandle(e){
        e.preventDefault();
        let formParams = {
            name: this.refs.name.value,
            password: this.refs.password.value
        }
        this.props.onSubmit(formParams);
    },
    render() {
        return (
            <Grid>
                <Form horizontal onSubmit={this.submitHandle}>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={4}>
                            
                        </Col>
                        <Col sm={8}>
                            <input className="form-control" ref="name" placeholder=""/>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col componentClass={ControlLabel} sm={4}>
                            
                        </Col>
                        <Col sm={8}>
                            <input className="form-control" type="password" ref="password" placeholder=""/>
                        </Col>
                    </FormGroup>
                    <FormGroup>
                        <Col smOffset={4} sm={8}>
                            <Button type="submit">
                                
                            </Button>
                        </Col>
                    </FormGroup>
                </Form>
            </Grid>
        );
    }
});
export default LoginForm;


