import React from 'react';
import {render} from 'react-dom';
import {Grid, Row, Col} from 'react-bootstrap';
import CommentList from './commentList'
const Comment = React.createClass({
    getDefaultProps(){
        return {
            data: []
        }
    },
    submitHandle(e){
        e.preventDefault();
        if (!this.refs.content.value){
            //
            return;
        }
        let newComment = {
            content: this.refs.content.value,
            name: this.refs.nickName.value||'',
            email: this.refs.email.value,
            url: this.refs.url.value
        }
        this.refs.addCommentForm.reset();
        this.props.onAddComment(newComment);
    },
    render(){
        let data = this.props.data;
        return (
            <div id="comments" className="comments-area">
                <h2 className="comments-title">
                    
                </h2>
                {/**/}
                <CommentList data={data}/>
                <div id="respond" className="comment-respond">
                    <h3 id="reply-title" className="comment-reply-title">
                        <div className="crunchify-text"></div>
                        {/*<small>*/}
                        {/*<!--<a rel="nofollow" id="cancel-comment-reply-link"-->*/}
                        {/*<!--href="/index.php/2016/09/11/hello-world/#respond"-->*/}
                        {/*<!--style="display:none;"></a>-->*/}
                        {/*</small>*/}
                    </h3>
                    <form method="post" ref="addCommentForm" className="comment-form" onSubmit={this.submitHandle}>
                        <p className="comment-notes">
                            {/*<!--<span id="email-notes"></span> -->*/}
                            {/*<!--<span class="required">*</span>-->*/}
                        </p>
                        <p className="comment-form-comment">
                            <textarea ref="content" name="content" placeholder="" cols="45" rows="8" aria-required="true">
                            </textarea>
                        </p>
                        <Row>
                            <Col md={4}>
                                <p className="comment-form-author">
                                    <input ref="nickName" placeholder="" name="name" type="text" size="30"/>
                                </p>
                            </Col>
                            <Col md={4}>
                                <p className="comment-form-email">
                                    <input ref="email" placeholder="email@domain.com" name="email" type="text" size="30"/>
                                </p>
                            </Col>
                            <Col md={4}>
                                <p className="comment-form-url">
                                    <input ref="url" name="website" type="text" placeholder="" size="30"/>
                                </p>
                            </Col>
                        </Row>
                        <p className="form-submit">
                            <input name="submit" type="submit" className="submit" value=""/>
                            <input type='hidden' name='comment_parent' value='0'/>
                        </p>
                    </form>
                </div>
            </div>
        )
    }
});
export default Comment;


